/** @jsxImportSource @emotion/react */

import { useLoaderData, useSearchParams } from '@remix-run/react'
import react, { useState, useEffect } from 'react'
import { HomeLoaderData } from './loader'

import { GamesCardCarousel } from './components/games-card-carousel'

import { Box, TextField, Typography } from '@mui/material'

import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider'
import { AdapterMoment } from '@mui/x-date-pickers/AdapterMoment'
import { DatePicker } from '@mui/x-date-pickers/DatePicker'

import moment from 'moment'

const ScheduledGamesTitle = () => {
  const [search, setSearch] = useSearchParams()
  const [dates, setDates] = useState({
    startDate: search.get('startDate') || moment().format('YYYY-MM-DD'),
    endDate:
      search.get('endDate') || moment().add(1, 'week').format('YYYY-MM-DD'),
  })

  return (
    <LocalizationProvider dateAdapter={AdapterMoment}>
      <Box display="flex" alignItems="center" justifyContent="space-between">
        <Typography
          align="left"
          variant="h2"
          sx={{ padding: '1rem', letterSpacing: '1.5px' }}
        >
          Scheduled Games
        </Typography>
        <Box display="flex" columnGap="1rem">
          <DatePicker
            views={['day']}
            label="Start Date"
            value={dates.startDate}
            onChange={(value) => {
              setDates({ ...dates, startDate: String(value) })
              search.set('startDate', String(value))
              setSearch(search, { replace: true })
            }}
            renderInput={(params) => (
              <TextField {...params} helperText={null} />
            )}
          />
          <DatePicker
            views={['day']}
            label="End Date"
            value={dates.endDate}
            onChange={(value) => {
              setDates({ ...dates, endDate: String(value) })
              search.set('endDate', String(value))
              setSearch(search, { replace: true })
            }}
            renderInput={(params) => (
              <TextField {...params} helperText={null} />
            )}
          />
        </Box>
      </Box>
    </LocalizationProvider>
  )
}

export function Home() {
  const { liveGames, scheduledGames, metaData } =
    useLoaderData<HomeLoaderData>()

  useEffect(() => {
    console.log(liveGames, scheduledGames)
    console.log('meta>>>>>', metaData)
  })

  return (
    <Box>
      <GamesCardCarousel games={liveGames} show={4} title="Today's Games" />
      <GamesCardCarousel
        games={scheduledGames}
        show={4}
        title={<ScheduledGamesTitle />}
      />
    </Box>
  )
}
